<template>
  <div class="company">
    <el-row>
      <el-col :span="6">
        <el-upload
          class="upload-demo"
          drag
          list-type="picture"
          :limit="1"
          :action="$baseURL+'/upload/uploadFileMasterGraph'"
          :headers="headers"
          multiple
          :file-list="fileList"
          :on-success="uploadSuccess"
          :on-error="uploadError"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将
            <span style="color: red;">LOGO</span>拖到此处，或
            点击上传
            <p style="color: #005aff;">建议468*60</p>
          </div>
        </el-upload>
      </el-col>
      <el-col :span="6">
        <el-upload
          class="upload-demo"
          drag
          list-type="picture"
          :limit="1"
          :action="$baseURL+'/upload/uploadFileMasterGraph'"
          :headers="headers"
          multiple
          :file-list="fileList1"
          :on-success="uploadSuccess1"
          :on-error="uploadError"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将
            <span style="color: red;">favicon</span>拖到此处，或
            点击上传
            <p style="color: #005aff;">建议64*64</p>
          </div>
        </el-upload>
      </el-col>
      
      <el-col :span="6">
        <el-upload
          class="upload-demo"
          drag
          list-type="picture"
          :limit="1"
          :action="$baseURL+'/upload/uploadFileMasterGraph'"
          :headers="headers"
          multiple
          :file-list="fileList2"
          :on-success="uploadSuccess2"
          :on-error="uploadError"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将
            <span style="color: red;">二维码</span>拖到此处，或
            点击上传
          </div>
        </el-upload>
      </el-col>
      
      <el-col :span="6">
        <el-button class="subBtn" type="primary" @click="submitArticle">提交</el-button>
      </el-col>
    </el-row>
    <br>
    <el-form ref="entity" :model="entity" label-width="80px">
      <el-form-item label="企业名称">
        <el-input v-model="entity.name" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="企业地址">
        <el-input v-model="entity.address" maxlength="200"></el-input>
      </el-form-item>
      <el-form ref="entity" :model="entity" label-width="80px">
        <el-form-item label="网站域名">
          <el-input v-model="entity.url" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="entity.email" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="entity.phone" maxlength="11"></el-input>
        </el-form-item>
      </el-form>
      <el-form-item label="版权信息">
        <el-input v-model="entity.copyright" maxlength="200"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "addArticle",
  data() {
    return {
      entity: {},
      fileList: [{ name: "LOGO", url: "" }],
      fileList1: [{ name: "favicon", url: "" }],
      fileList2: [{ name: "favicon", url: "" }],
      headers: { token: this.$store.getters.token }
    };
  },
  created() {
    this.findById(1);
  },
  methods: {
    findById(id) {
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/company/find",
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.entity = response.data;
          this.fileList[0].url = this.entity.logo;
          this.fileList1[0].url = this.entity.favicon;
          this.fileList2[0].url = this.entity.qrcode;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    },
    uploadSuccess(response, file, fileList) {
      console.log(response, file, fileList);
      if (response.ok) {
        this.entity.logo = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },uploadSuccess2(response, file, fileList) {
      console.log(response, file, fileList);
      if (response.ok) {
        this.entity.qrcode = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },uploadSuccess1(response, file, fileList) {
      console.log(response, file, fileList);
      if (response.ok) {
        this.entity.favicon = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList1 = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },
    uploadError(error, file, fileList) {
      console.log(error, file, fileList);
      this.$message({
        showClose: true,
        message: "上传失败！",
        type: "error"
      });
      return false;
    },

    submitArticle() {
      this.$axios({
        method: "put",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/company/updateById",
        data: this.entity,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
      console.log("源码", this.entity);
    }
  }
};
</script>

<style >
.upload-demo {
  width: 80%;
  /* margin: 0 auto; */
  position: relative;
}
.el-upload-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 28px;
  left: 10px;
}
.upload {
  background: #fff;
  border: 0;
  color: #409eff;
}
.articleCategory {
  width: 20%;
  margin-left: 0px;
}
.company .subBtn {
  width: 60%;
  margin: 0 auto;
  float: right;
}
.keyword {
  float: left;
  width: 48%;
}
.hot {
  width: 18%;
  margin: 10px 8px;
  float: left;
}
</style>



